.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #ffffff;
  font-family: 'Segoe UI', sans-serif;
}

.app-title {
  text-align: center;
  font-size: 1.8rem;
}

.geo-chart {
  width: 100%;
  max-width: 900px;
  aspect-ratio: 16 / 9;
}

.level-label {
  font-size: 1.2rem;
  font-weight: 500;
  color: #444;
  margin-bottom: 20px;
}

.checkbox-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 32px 24px;
  margin-top: 40px;
  width: 100%;
  max-width: 1000px;
}

.checkbox-group {
  display: flex;
  flex-direction: column;
}

.checkbox-group h3 {
  margin-bottom: 12px;
  font-size: 1.2rem;
  font-weight: bold;
  text-align: left;
}

.checkbox-item {
  display: inline-block;
  margin-bottom: 6px;
  font-size: 0.95rem;
  white-space: nowrap;
}
